  position: relative;
}

.flipclock .flip-clock-wrapper * {
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box;
  backface-visibility: hidden;
}

.flipclock .flip-clock-wrapper a {
  cursor: pointer;
  color: #ccc;
  text-decoration: none;
}

.flipclock .flip-clock-wrapper a:hover {
  color: #fff;
}

.flipclock .flip-clock-wrapper .flip-wrapper {
  text-align: center;
  display: inline-block;
}

.flipclock .flip-clock-wrapper .flip-wrapper + .flip-wrapper {
  margin-left: 10px;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip {
  float: none;
  width: 64px;
  height: 60px;
  box-shadow: none;
  background: #000;
  border-radius: 6px;
  max-height: 60px;
  margin: 0;
  font-family: PT Sans, sans-serif;
  font-weight: 400;
  line-height: 87px;
  list-style: none;
  position: relative;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip li {
  z-index: 1;
  width: 100%;
  height: 100%;
  line-height: 53px;
  position: absolute;
  top: 0;
  left: 0;
  text-decoration: none !important;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip li:first-child {
  z-index: 2;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip li a {
  perspective: 200px;
  height: 100%;
  display: block;
  cursor: default !important;
  margin: 0 !important;
  overflow: visible !important;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip li a div {
  z-index: 1;
  outline: 1px solid #0000;
  width: 100%;
  height: 50%;
  font-size: 80px;
  position: absolute;
  left: 0;
  overflow: hidden;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip li a div .shadow {
  z-index: 2;
  width: 100%;
  height: 100%;
  position: absolute;
