  border-radius: 6px;
  width: 100%;
  height: 200%;
  font-size: 40px;
  line-height: 64px;
  position: absolute;
  left: 0;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip li a .up {
  transform-origin: 50% 100%;
  top: 0;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip li a .up:after {
  content: "";
  z-index: 5;
  background-color: #fefefe33;
  width: 100%;
  height: 3px;
  position: absolute;
  top: 29px;
  left: 0;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip li a .up .counter {
  filter: progid:DXImageTransform. Microsoft. gradient(startColorstr= "#212121", endColorstr= "#323338", GradientType= 0);
  background: linear-gradient(#212121 0%, #323338 100%);
  top: 0;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip li a .down {
  transform-origin: 50% 0;
  border-bottom-right-radius: 6px;
  border-bottom-left-radius: 6px;
  bottom: 0;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip li a .down .counter {
  filter: progid:DXImageTransform. Microsoft. gradient(startColorstr= "#323338", endColorstr= "#212121", GradientType= 0);
  background: linear-gradient(#323338 0%, #212121 100%);
  bottom: 0;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip li.flip-clock-active, .flipclock .flip-clock-wrapper .flip-wrapper .flip.play .flip-clock-before {
  z-index: 3;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip.play .flip-clock-before .up {
  z-index: 2;
  animation: .5s cubic-bezier(.34, .13, .93, .24) both turnoff;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip.play .flip-clock-before .up .shadow {
  background: -webkit-gradient(linear, left top, left bottom, from(#0000001a), to(#000));
  background: linear, top, #0000001a 0, #000 100%;
  background: -ms-linear-gradient(top, #0000001a 0, #000 100%);
