.flipclock .flip-clock-wrapper .flip-wrapper .flip.play .flip-clock-before .down .shadow {
  background: -webkit-gradient(linear, left top, left bottom, from(#000), to(#0000001a));
  background: linear, top, #000 0, #0000001a 100%;
  background: -ms-linear-gradient(top, #000 0, #0000001a 100%);
  background: linear, to bottom, #000 0, #0000001a 100%;
  background: -moz-linear-gradient(top, #000 0, #0000001a 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(#000), to(#0000001a));
  background: -moz-linear-gradient(top, #000 0, #0000001a 100%);
  animation: .5s linear both show;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip.play .flip-clock-active {
  z-index: 5;
  animation: .5s linear .5s both zindex;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip.play .flip-clock-active .up .shadow {
  background: -webkit-gradient(linear, left top, left bottom, from(#0000001a), to(#000));
  background: linear, top, #0000001a 0, #000 100%;
  background: -ms-linear-gradient(top, #0000001a 0, #000 100%);
  background: linear, to bottom, #0000001a 0, #000 100%;
  background: -moz-linear-gradient(top, #0000001a 0, #000 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(#0000001a), to(#000));
  background: -moz-linear-gradient(top, #0000001a 0, #000 100%);
  animation: .5s linear .3s both hide;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip.play .flip-clock-active .down {
  z-index: 2;
  animation: .3s linear .5s both turnon;
}

.flipclock .flip-clock-wrapper .flip-wrapper .flip.play .flip-clock-active .down .shadow {
  background: -webkit-gradient(linear, left top, left bottom, from(#000), to(#0000001a));
  background: linear, top, #000 0, #0000001a 100%;
  background: -ms-linear-gradient(top, #000 0, #0000001a 100%);
  background: linear, to bottom, #000 0, #0000001a 100%;
  -webkit-animation: .5s linear .3s both hide;
  background: -moz-linear-gradient(top, #000 0, #0000001a 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(#000), to(#0000001a));
  background: -moz-linear-gradient(top, #000 0, #0000001a 100%);
  animation: .5s linear .2s both hide;
}

.flipclock .flip-clock-wrapper.clearfix:after {
  content: " ";
  clear: both;
  display: table;
}

.flipclock .flip-clock-wrapper.clearfix:before {
  content: " ";
  display: table;
}

@keyframes zindex {
  0% {
    z-index: 2;
  }

  20% {
    z-index: 3;
  }

  100% {
    z-index: 4;
  }
}

@-webkit-keyframes turnon {
  0% {
    -webkit-transform: rotateX(91deg);
  }

  100% {
    -webkit-transform: rotateX(0);
  }
}

@keyframes turnon {
  0% {
    transform: rotateX(91deg);
  }

  100% {
    transform: rotateX(0);
  }
}

@-webkit-keyframes turnoff {
  0% {
    -webkit-transform: rotateX(0);
  }

  100% {
    -webkit-transform: rotateX(-90deg);
  }
}

@keyframes turnoff {
  0% {
    transform: rotateX(0);
  }

  100% {
    transform: rotateX(-90deg);
  }
}

@keyframes show {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes hide {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}
